:root {
  --global-bg-color: #{$new-navy};
  --global-code-bg-color: #{$lighter-navy};
  --global-bg-color-light: #{$new-navy-light};
  --global-text-color: #{$morning};
  --global-text-color-light: #{$morning-light};
  --global-theme-color: #{$spring-green};
  --global-hover-color: #{$spring-green};
  --global-distill-app-color: #{$grey-color-light};
  --global-button-color1: #{$sg-button-dark};
  --link-color: #{$spring-green};
  --link-shadow5: #{$baby-pink-muted5};
  --link-shadow6: #{$baby-pink-muted6};
  --link-shadow9: #{$baby-pink-muted9};
  .fa-sun {
    display : none;
  }
  .fa-moon {
    padding-left: 10px;
    padding-top: 12px;
    display : block;
  }
}

