html[data-theme='dark'] {
  --global-bg-color: #{$navy};
  --global-code-bg-color: #{$lighter-navy};
  --global-bg-color-light: #{#light-navy};
  --global-text-color: #{$light-slate};
  --global-text-color-light: #{$lighter-slate};
  --global-theme-color: #{$washed-red};
  --global-hover-color: #{$washed-red};
  --global-button-color1: #{$wr-button-dark};
  --link-color: #{$washed-red};
  --link-shadow5: #{$gc-muted5};
  --link-shadow6: #{$gc-muted6};
  --link-shadow9: #{$gc-muted9};

  .fa-sun {
    padding-left: 10px;
    padding-top: 12px;
    display : block;
  }
  .fa-moon {
    display : none;
  }
}
