/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

//Fonts

@font-face {
  font-family: "calibre";
  src: url("../fonts/Calibre/Calibre-Regular.woff");
  font-display: swap;
}

@font-face {
  font-family: "calibre-light";
  src: url("../fonts/Calibre/Calibre-Light.woff");
  font-display: swap;
}

@font-face {
  font-family: "calibre-semibold";
  src: url("../fonts/Calibre/Calibre-Semibold.woff");
  font-display: swap;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');
@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100;200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap');
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@200;300;400;500&display=swap');



// Typography
p, h1, h2, h3, h4, h5, h6, em, div, span, strong {
  color: var(--global-text-color);
  font-family: "calibre-light", sans-serif;
  font-size: 24px;
}

