// LINK EXPERIMENTATION

a {
  text-decoration: none i !important;
  text-shadow: 0 2px 3px var(--link-shadow5);
  transition: all .3s ease-in;
  position:relative;
}

@media screen and (max-width: 599px) {
  a, a:active, a:focus, a:link, a:visited {
      color: var(--link-color);
  }
}

@media screen and (min-width: 600px) {
  a, a:active, a:focus, a:link, a:visited {
    color: var(--link-color);
  }
}

a:hover {
  position: relative;
  text-shadow:0 2px 10px var(--link-shadow9);
  text-decoration: none !important;
}

a.button:hover, a.resp-sharing-button__link:hover {
  -webkit-animation: none;
  animation:none
}

a:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background-color: var(--link-color);
  -webkit-transform-origin: bottom right;
  transform-origin: bottom right;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  transition: -webkit-transform .5s ease;
  transition: transform .5s ease;
  transition: transform .5s ease, -webkit-transform .5s ease;
  box-shadow:0 2px 5px var(--link-shadow6);
}

a:hover:before {
  -webkit-transform-origin: bottom left;
  transform-origin: bottom left;
  -webkit-transform: scaleX(1);
  transform:scaleX(1)
}

// For special link nohover
a.nohover:hover {
  position: relative;
  text-shadow: none;
  text-decoration: none !important;
}

a.button:hover, a.resp-sharing-button__link:hover {
  -webkit-animation: none;
  animation:none
}

a.nohover:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background-color: transparent;
  -webkit-transform-origin: bottom right;
  transform-origin: bottom right;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  transition: -webkit-transform .5s ease;
  transition: transform .5s ease;
  transition: transform .5s ease, -webkit-transform .5s ease;
  box-shadow: none;
}

a.nohover:hover:before {
  -webkit-transform-origin: bottom left;
  transform-origin: bottom left;
  -webkit-transform: scaleX(1);
  transform:scaleX(1)
}

//EXPERIMENT OVER

.name {
  color: var(--global-text-color-light);
  font-family: "calibre-semibold", sans-serif;
  font-size: 7rem;
  white-space: nowrap;
  overflow: hidden;
  border-right: solid .2ch var(--global-text-color-light);
  width: 10.5ch;
}

.name {
  animation: animated-text 2s linear .5s 1 normal both,
             animated-cursor 1s linear infinite;
}

/* text animation */

@keyframes animated-text{
  from{width: 0;}
  to{width: 10.5ch;}
}

/* cursor animations */

@keyframes animated-cursor{
  from{border-right-color: var(--global-text-color-light);}
  to{border-right-color: transparent;}
}

.name-color {
  color: var(--global-theme-color);
  // font-family:'Courier New', Courier, monospace;
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 400;
  font-size: 20px;
}

.about {
  float: left;
  width: 60%;
  line-height: normal;
}
@media screen and (max-width: 500px)  {
  .about {width: 100%; }
  .name {font-size: 2.5rem;}
  .name-color {font-size: 18px;}
}

::-moz-selection { /* Code for Firefox */
  color: var(--global-text-color-light); /* needs to be set as global for light mode */
  background: var(--global-code-bg-color);
}

::selection {
  color: var(--global-text-color-light); /* needs to be set as global for light mode */
  background: var(--global-code-bg-color);
}


// Profile

.profile {
  margin-left: 1rem;
  width: 100%;

  .address {
    margin-bottom: 5px;
    margin-top: 5px;
    font-family: monospace;
    font-size: 1.2rem;
    p {
      display: inline-block;
      margin: 0;
    }
  }
}

@media (min-width: 576px) {
  .profile {
    width: 30%;
    .address {
      p { display: block; }
    }
  }
}

.post-description {
  margin-bottom: 2rem;
  font-size: 0.875rem;
  a {
    color: inherit;
    &:hover {
      color: var(--global-theme-color);
      text-decoration: none;
    }
  }
}


// Navbar customization

.button-resume {
  background-color: transparent; /* Green */
  border: 1px solid var(--global-theme-color);
  color: var(--global-theme-color);
  font-family: 'JetBrains Mono', monospace;
  padding: 1px 10px;
  text-align: justify;
  text-decoration: none;
  display: inline-block;
  font-size: 17px;
  margin: 10px 10px;
  cursor: pointer;
  border-radius: 8px;
  transition: box-shadow 0.5s ease-in-out, border-color 0.5s ease-in-out;
}
.button-resume:hover {
  background-color: var(--global-theme-color);
  color: black;
}

.navbar {
  box-shadow: none;
  border-bottom: 0px solid $grey-color-light;
  background-color: var(--global-bg-color);
  opacity: 0.95;
}
.navbar.navbar-light {
  a {
    &:hover {
      text-decoration: none;
    }
  }
  .navbar-brand {
    color: var(--global-text-color);
  }
  .navbar-nav .nav-item .nav-link {
    color: var(--global-text-color);
    &:hover {
      color: var(--global-hover-color);
    }
  }
  .navbar-nav .nav-item.active>.nav-link {
      background-color: inherit;
      font-weight: bolder;
      color: var(--global-theme-color);
      &:hover {
        color: var(--global-hover-color);
      }
  }
  .navbar-brand.social {
    padding-bottom: 0;
    padding-top: 0;
    font-size: 1.7rem;
    a {
      i::before {
        color: var(--global-text-color);
        -webkit-transition: all 0.2s ease-in-out;
      }
      &:hover {
        i::before {
          color: var(--global-theme-color);
        }
      }
    }
  }
}

.navbar-toggler {
  .icon-bar {
    display: block;
    width: 22px;
    height: 2px;
    background-color: var(--global-text-color);
    border-radius: 1px;
    margin-bottom: 4px;
    transition: all 0.2s;
  }
  .top-bar {
    transform: rotate(45deg);
    transform-origin: 10% 10%;
  }
  .middle-bar {
    opacity: 0;
  }
  .bottom-bar {
    transform: rotate(-45deg);
    transform-origin: 10% 90%;
  }
}

.navbar-toggler.collapsed {
  .top-bar {
    transform: rotate(0);
  }
  .middle-bar {
    opacity: 1;
  }
  .bottom-bar {
    transform: rotate(0);
  }
}

// Blog

.header-bar {
  border-bottom: 1px transparent $grey-color-light;
  text-align: center;
  padding-top: 2rem;
  padding-bottom: 5rem;
  h1 {
    color: var(--global-theme-color);
    font-size: 10rem;
    font-weight: bold;
  }
}

.post-list {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  li {
    border: 1px transparent var(--global-theme-color);
    list-style: none;
    margin-top: 2rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    background-color: var(--global-code-bg-color);
    padding-left: 2rem;
    border-radius: 10px;
    .post-meta {
      color: var(--global-text-color-light);
      font-size: 0.875rem;
      margin-bottom: 0;
    }
    a {
      text-decoration: none;
      font-size: 2rem;
      color: var(--global-text-color-light);
      &:hover {
        color: var(--global-theme-color);
      }
    }
  }
}

.pagination {
  .page-item {
    .page-link {
      color: var(--global-text-color);
      &:hover {
        color: $black-color;
      }
    }
    &.active .page-link {
      background-color: var(--global-theme-color);
      &:hover {
        background-color: var(--global-theme-color);
      }
    }
  }
}


// Distill

.distill {
  a:hover {
    border-bottom-color: var(--global-theme-color);
    text-decoration: none;
  }
}


// Rouge Color Customization
figure.highlight {
  margin: 0 0 1rem;
}

pre {
  color: var(--global-text-color-light);
  background-color: var(--global-code-bg-color);
  border-radius: 6px;
  padding: 6px 12px;
  font-family: 'Roboto Mono', monospace;
  font-size: 1rem;
  pre, code {
    background-color: transparent;
    border-radius: 0;
    padding: 0;
  }
}

code {
  color: var(--global-text-color-light);
  background-color: var(--global-code-bg-color);
  border-radius: 2px;
  padding: 2px 2px;
}


// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
  transition: all 750ms !important;
  transition-delay: 0 !important;
}


